import React, { Component } from 'react';

const tabela = [
    { "Quantidade de peças": "6 Peças" },
    { "Itens inclusos": "6 Taças de Cerveja de 330 mL" },
    { "Material": "Vidro" },
    { "Cor": "Transparente" },
    { "Acabamento": "Liso" },
    { "Formato": "Redondo" },
    { "Dicas de uso e preservação": "Não usar produtos abrasivos\nLavar com sabão neutro\nUsar esponja macia\nSecar bem após a lavagem" },
    { "Vai à lava louças": "Sim" },
    { "Dimensões do produto (A x L x C)": "18,2 x 15,3 cm" }
]

class Descricao extends Component {

    renderDetalhes(){
        return (
            <div>
                <span>O Jogo de Taças de Cerveja Windsor 6 Peças - Nadir Figueiredo é ideal para desfrutar com os amigos deliciosas cervejas com design moderno e arrojado.</span>
                <span>Peças indispensáveis para a composição de uma mesa com elegância e modernidade. </span>
                <span>Traga praticidade e funcionalidade no seu dia a dia. </span>
                <span>O vidro cristalino e brilhante traz qualidade e resistência ao produto.</span>
            </div>
        )
    }

    renderTabela(){
        return (
            <div className="container-small">
                <table className="table">
                    <tbody>
                        {
                            tabela.map((item, idx) => (
                                <tr key={idx}>
                                    <td>{Object.keys(item)[0]}</td>
                                    <td>{item[Object.keys(item)[0]]}</td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        )
    }

    render(){
        return (
            <div className="Descricao container">
                <div className="card no-mb-margin">
                    <div>
                        <h2>Detalhes/Descrição</h2>
                        <br />
                        { this.renderDetalhes() }
                        <br /><br />
                        { this.renderTabela() }
                    </div>
                </div>
            </div>
        )
    }
}

export default Descricao;